.header {
  margin-top: 60px;

  &__titles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Lora", serif;
    color: #444;
  }

  &__title-sm {
    position: absolute;
    top: 18%;
    font-size: 20px;
  }

  &__title-lg {
    position: absolute;
    top: 20%;
    font-size: 100px;
  }

  &__img {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
  }
}
