.top {
  width: 100%;
  height: 50px;
  background-color: white;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  z-index: 10;

  &__left,
  &__right {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__center {
    flex: 6;
  }

  &__icon {
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
  }

  &__img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }

  &__list {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;

    &-item {
      margin-right: 20px;
      font-size: 18px;
      font-weight: 300;
      cursor: pointer;
    }
  }

  &__search-icon {
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
  }
}
